import styles from './order-details.module.css';
import orderDone from '../../images/done.svg';
import {useSelector} from "react-redux";

const OrderDetails = () => {
    const orderData = useSelector(store => store.order);

    return (
        <>
            {orderData.postOrderRequest && (<p className={"text_type_main-medium mt-30 mb-30"}>...</p>)}
            {orderData.postOrderError && (<p className={"text_type_main-medium mt-30 mb-30"}>: {orderData.error}</p>)}
            {orderData.order && (
                <>
                    <h3 className={styles.orderDetails__number}>{orderData.order.number}</h3>
                    <p className={styles.orderDetails__numberSubtitle}> </p>
                    <img className={styles.orderDetails__doneIcon} src={orderDone} alt={` ""`}/>
                    <p className={styles.orderDetails__doneSubtitle}>   </p>
                    <p className={styles.orderDetails__waitSubtitle}>    </p>
                </>
            )}
        </>
    )
}

export default OrderDetails;